<script>
import axios from "axios";

export default {
  data() {
    return {
      paramsId: 0,
      error: {
        isError: false,
        errorMsg: "",
      },
      uri: "",
      marketName: "",
      marketType: "",
      showCard: {
        showVega: false,
        showMeat: false,
      },
      isTaiMarket: false,
      taiFunction: false,
      isCornerMarket: false,
      isSriMarket: false,
      cornerFunction: false,
      isLoad: false,
      product: [],
      search: "",
      vegaSelect: [
        "",
        "",
        "",
        "",
        "",
        " ",
        " ()",
        "",
      ],
      meatSelect: ["", "", ""],
      riceSelect: [""],
      fieldSelect: [""],
      dryFoodSelect: ["", ""],
      plantSelect: ["", "", ""],
      headerProps: {
        sortByText: "Custom sort by",
      },
      headers: [],
    };
  },
  methods: {
    getColor(dif) {
      if (dif.charAt(0) === "-") {
        return "red";
      } else if (dif.substring(0, 4) === "0.00") {
        return "grey";
      } else {
        return "green";
      }
    },
    checkDif(dif) {
      if (dif.charAt(0) === "-") {
        return "mdi-arrow-down";
      } else if (dif.substring(0, 4) === "0.00") {
        return "mdi-minus";
      } else {
        return "mdi-arrow-up";
      }
    },
    getProductData(uri, marketName) {
      this.product = [];
      this.headers = [
        { text: "", value: "post_title" },
        { text: " / ", value: "range_value" },
        { text: " / ", value: "priceDif" },
      ];
      this.uri = uri;
      this.marketName = marketName;
      if (this.uri !== "") {
        this.isTaiMarket = false;
        this.showCard.showVega = false;
        this.isCornerMarket = false;
        this.taiFunction = true;
        this.cornerFunction = false;
        axios
          .get(this.uri)
          .then((response) => {
            this.product = response.data.posts;
            response.data.posts.map((data, index) => {
              this.product[index].post_title = data.post_title.substring(10);
              this.product[index].range_value = this.product[
                index
              ].acf.recent_price.low_price
                .concat(" - ")
                .concat(
                  this.product[index].acf.recent_price.high_price.concat(" ")
                );
              this.product[index].priceDif = (
                data.acf.recent_price.difference * 100
              )
                .toFixed(2)
                .concat(" %");
            });
            this.isLoad = false;
            console.log(this.product);
          })
          .catch((err) => {
            if (err) this.isLoad = false;
          });
      }
    },
    showTable() {
      this.isLoad = true;

      switch (this.marketName) {
        case "":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7324&per_page=500",
            " > "
          );
          break;
        case "":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7326&per_page=500",
            " > "
          );
          break;
        case "":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7331&per_page=500",
            " > "
          );
          break;
        case "":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7334&per_page=500",
            " > "
          );
          break;
        case "":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=4951&per_page=500",
            " > "
          );
          break;
        case " ":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7318&per_page=500",
            " >  "
          );
          break;
        case " ()":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7306&per_page=500",
            " ()"
          );
          break;
        case "":
          this.getProductData(
            "https://cms.talaadthai.com/wp-json/tlt/v1/products_from_subcat_id?id=7310&per_page=500",
