<script>
import axios from "axios";
import ThailandAutoComplete from "vue-thailand-address-autocomplete";

export default {
  components: {
    ThailandAutoComplete,
  },
  data: () => ({
    isSelectProvince: false,
    isFormValid: false,
    confirmPassword: "",
    sellerForm: {
      idTypeOfSeller: "",
      prefix: "",
      firstName: "",
      lastName: "",
      username: "",
      phoneNumber: "",
      email: "",
      idCard: "",
      gender: "",
      password: "",
      vProvinceId: "",
      vLinkAreaDetailId: "",
      productType: [],
      address: "",
      moo: "",
      road: "",
      district: "",
      amphoe: "",
      zipcode: "",
      groupName: "",
      shopName: "",
      brandName: "",
      lineId: "",
      facebookName: "",
    },
    isRegisForm: {
      buyer: false,
      seller1: false,
    },
    isAgree: "",
    valid: false,
    userType: "",
    isType: true,
    isShowPolicy: false,
    policy: {
      showBuyerPolicy: false,
      showSellerPolicy: false,
      title: "",
      titleColor: "",
      ruleTitle: "",
      firstRule: "",
      secRule: "",
      thirdRule: "",
      fourthRule: "",
      fifthRule: "",
      sixthRule: "",
      seventhRule: "",
      eigthRule: "",
      ninethRule: "",
      footer: "",
    },
    prefix: [],
    area: [],
    sellType: [],
    province: [
      {
        provinceName: "",
      },
    ],
    targetName: [],
    targetFilter: [],
    firstNameRules: [(v) => !!v || ""],
    lastNameRules: [(v) => !!v || ""],
    emailRules: [
      (v) => !!v || "",
      (v) => /.+@.+/.test(v) || "",
    ],
    phoneRules: [
      (v) => !v.length < 10 || " 10 ",
      (v) => !!v || "",
      (v) =>
        /^0[0-9]{9}/.test(v) || "",
    ],
    cardRules: [
      (v) => !!v || "",
      (v) => v.length >= 13 || " 13 ",
    ],
    passwordRules: [
      (v) => !!v || "",
      (v) => v.length >= 6 || " 6 ",
      (v) => /[A-Z]+/.test(v) || "",
      (v) => /.{8,}/.test(v) || "",
      (v) => /^[a-zA-Z0-9]*$/.test(v) || "",
      (v) =>
      (v || "").indexOf(" ") < 0 || "",
    ],
  }),
  methods: {
    select(address) {
      this.sellerForm.district = address.district;
      this.sellerForm.amphoe = address.amphoe;
      this.sellerForm.zipcode = address.zipcode;
    },
    findAreaByProvince(provinceId) {
      axios
        .get(
          `https://farmtd.hrdi.or.th/farmdd/api/getVLinkTargetById.php?provinceId=${provinceId}`
        )
        .then((res) => {
          console.log("vTargetDetail", res.data.vTargetDetail);
          if (res.data.vTargetDetail.length > 0) {
            this.targetName = [
              ...new Map(
                res.data.vTargetDetail.map((item) => [item.targetName, item])
              ).values(),
            ];
            this.getArea();
          }
        });
      this.isSelectProvince = true;
    },
    getArea() {
      axios
        .get(`https://farmtd.hrdi.or.th/farmdd/api/getAllArea.php`)
        .then((res) => {
          this.targetName = this.targetName.map((item) => item.targetId);
          console.log("targetName", this.targetName);
          if (res.data.area.length > 0) {
            this.area = res.data.area;
          }
          this.targetFilter = res.data.area.filter((item) =>
            this.targetName.includes(item.idArea)
          );
          this.isSelectProvince = true;
          console.log("targetFilter", this.targetFilter);
        });
    },
    checkLoginType(type) {
      this.isType = false;
      this.isShowPolicy = true;
      if (type === "Buyer") {
        this.policy = {
          showBuyerPolicy: true,
          showSellerPolicy: false,
          title: "",
          titleColor: "indigo--text",
          ruleTitle:
            "       ",
          firstRule:
            "1.   ",
          secRule:
            "2.     ",
          thirdRule:
            "3.     ",
          fourthRule:
            "4.      ",
          fifthRule:
            "5.      ",
          sixthRule:
            "6.        ",
          seventhRule:
            "7.     ",
          eigthRule:
            "8.          ",
          footer:
            '    /       "" ',
        };
      }

      if (type === "Seller") {
        this.policy = {
          showBuyerPolicy: false,
          showSellerPolicy: true,
          title: "",
          titleColor: "pink--text",
          ruleTitle:
            "       ",
          firstRule:
            "1.  ",
          secRule:
            "2.   ",
          thirdRule:
            "3.     ",
          fourthRule:
            "4.     ",
          fifthRule:
            "5.      ",
          sixthRule:
            "6.      ",
          seventhRule:
            "7.        ",
          eigthRule:
            "8.     ",
          ninethRule:
            "9.          ",
          footer:
            '    /       "" ',
        };
        console.log(this.policy);
      }
    },
    acceptPolicyCheck(isAccept, Type) {
      console.log(isAccept);
      if (!isAccept || isAccept === "Unaccecpt" || isAccept === null) {
        this.$swal.fire({
          icon: "warning",
          title: "",
          text: "",
          allowOutsideClick: false,
        });
        return;
      }

      this.isShowPolicy = false;

      if (isAccept && Type === "Buyer") {
        this.isRegisForm = {
          buyer: true,
          seller1: false,
        };
        console.log(this.isRegisForm);
      }

      if (isAccept && Type === "Seller") {
        this.isRegisForm = {
          buyer: false,
          seller1: true,
        };
      }
    },
    checkSellerRegister() {
      console.log(this.sellerForm);
      if (
        this.sellerForm.prefix === "" ||
        this.sellerForm.prefix === null ||
        this.sellerForm.prefix === undefined
      ) {
        this.alertCheck("");
        return;
      }

      if (
        this.sellerForm.firstName === "" ||
        this.sellerForm.firstName === null ||
        this.sellerForm.firstName === undefined
      ) {
        this.alertCheck("");
        return;
      }

      if (
        this.sellerForm.lastName === "" ||
        this.sellerForm.lastName === null ||
        this.sellerForm.lastName === undefined
      ) {
        this.alertCheck("");
        return;
      }

      if (
        this.sellerForm.email === "" ||
        this.sellerForm.email === null ||
        this.sellerForm.email === undefined
      ) {
        this.alertCheck("");
        return;
      }

      if (
        this.sellerForm.idCard === "" ||
        this.sellerForm.idCard === null ||
        this.sellerForm.idCard === undefined
      ) {
        this.alertCheck("");
        return;
      }

      if (
        this.sellerForm.idCard === "" ||
        this.sellerForm.idCard === null ||
        this.sellerForm.idCard === undefined
      ) {
        this.alertCheck("");
        return;
      }

      var formData = new FormData();
      formData.append("address", this.sellerForm.address);
      formData.append("amphoe", this.sellerForm.amphoe);
      formData.append("brandName", this.sellerForm.brandName);
      formData.append("district", this.sellerForm.district);
      formData.append("email", this.sellerForm.email);
      formData.append("facebookName", this.sellerForm.facebookName);
      formData.append("firstName", this.sellerForm.firstName);
      formData.append("gender", this.sellerForm.gender);
      formData.append("groupName", this.sellerForm.groupName);
      formData.append("idCard", this.sellerForm.idCard);
      formData.append("idTypeOfSeller", this.sellerForm.idTypeOfSeller);
      formData.append("lastName", this.sellerForm.lastName);
      formData.append("lineId", this.sellerForm.lineId);
      formData.append("moo", this.sellerForm.moo);
      formData.append("password", this.sellerForm.password);
      formData.append("phoneNumber", this.sellerForm.phoneNumber);
      formData.append("prefix", this.sellerForm.prefix);
      formData.append("productType", this.sellerForm.productType);
      formData.append("road", this.sellerForm.road);
      formData.append("shopName", this.sellerForm.shopName);
      formData.append("username", this.sellerForm.username);
      formData.append("vLinkAreaDetailId", this.sellerForm.vLinkAreaDetailId);
      formData.append("vProvinceId", this.sellerForm.vProvinceId);
      formData.append("zipcode", this.sellerForm.zipcode);
      var contentForm = {};
      formData.forEach(function (value, key) {
        console.log(value);
        contentForm[key] = value;
      });

      axios
        .post(
          "https://farmtd.hrdi.or.th/farmdd/api/postAddSellerMember.php",
          formData
        )
        .then((res) => {
          if (res.data === "True") {
            this.$swal
              .fire({
                icon: "success",
                title: "",
                text: "",
                showConfirmButton: false,
                timer: 2000,
              })
              .then(() => {
                this.$router.push({ name: "Login" });
              });
          } else {
            this.$swal
              .fire({
                icon: "error",
                title: "",
                text: "",
                showConfirmButton: false,
                timer: 2000,
              })
              .then(() => {
                this.$router.push({ name: "Login" });
              });
          }
        });
    },
    alertCheck(msg) {
      this.$swal.fire({
        icon: "warning",
        title: msg,
        allowOutsideClick: false,
      });
    },
    getPrefix() {
      axios
        .get("https://farmtd.hrdi.or.th/farmdd/api/getPrefix.php")
        .then((res) => {
          this.prefix = res.data.prefix;
          console.log("prefix", this.prefix);
        });
    },
    getProvince() {
      axios
        .get("https://farmtd.hrdi.or.th/farmdd/api/getVLinkProvince.php")
        .then((res) => {
          if (res.data.vLinkProvince.length > 0) {
            this.province = [
              ...new Map(
                res.data.vLinkProvince.map((item) => [item.provinceId, item])
              ).values(),
            ];
          }

          this.province = this.province.filter((item, index) => {
            return (
              item.provinceName !== "" &&
              item.provinceName !== "" &&
              item.provinceName !== ""
            );
          });

          console.log("province", this.province);
        });
    },
    seller2Revert() {
      this.isRegisForm = {
        seller1: true,
        seller2: false,
        seller3: false,
      };
    },
    getSellerType() {
      axios
        .get("https://farmtd.hrdi.or.th/farmdd/api/getSellerType.php")
        .then((res) => {
          this.sellType = res.data.sellType;
          console.log("sellType", this.sellType);
        });
    },
  }, // End Method
  mounted() {
    this.getPrefix();
    this.getProvince();
    this.getSellerType();
  },
};
</script>
<style scoped>
#rounded {
  border-radius: 5px;
}
</style>



